    width:400px;
    height: 130px;
    overflow: hidden;
}

#nobot div.m{
    margin-right:10px;
    margin-left:10px;
    width:20px;
    float:left;
    border: 1px solid black;
    border-radius: 5px;
    box-sizing: initial;
    line-height: initial;
    text-align: center;
}

#nobot div.d{
    /* border:1px solid black; */
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    width:20px;
    height: 17px;
    overflow: hidden;
}

#nobot div.xb{

}


#nobot div span{
    display:block;
    line-height: 17px;
    text-align: center;
}

#nobot div.d div{
position: relative;
}


.loader {
    width: 48px;
    height: 48px;
    border: 5px solid #FFF;
    border-bottom-color: #FF3D00;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    }

    @keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
    } 


#nobot {
    position: relative;
    }

.overlay{
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: #fff;
    width: 100%;
    height: 100%;
}

.p1, .p2,.p3,.p4,.p5,.p6,.p7,.p8,.p9{
    transition: top 0.5s;
}

.p1{
top:0px;
}

.p2{
    top:-17px;
}

.p3{
    top:-34px;
}
.p4{
    top:-51px;
}
.p5{
    top:-68px;
}
.p6{
    top:-85px;
}
.p7{
    top:-102px;
}
.p8{
    top:-119px;
}


.bu,.bd{
    border:none;
    background-color: transparent;
}

.question{
    margin-bottom: 10px;
}

.validate{
    margin-top: 31px;
}

